import React from 'react'

export const Contact = () => {
    return (
        // eslint-disable-next-line react/style-prop-object
        <div class="container">
        <div>
            <center><h2>Contact Me</h2>
            <p>This page is meant to create a connection between user and developer. You can Contact me using folllowing emails or using the form. <br/><br/>

            <p>
                <form style={{width: "50%"}}>
                    <div class="input-group mb-2">
                        <div class="input-group-prepend">
                            <div id="email" class="input-group-text">Your Name</div>
                        </div>
                        <input type="text" class="form-control" placeholder="name surname"/>
                    </div> <br/>

                    <div class="input-group mb-2">
                        <div class="input-group-prepend">
                            <div class="input-group-text">Your Email</div>
                        </div>
                        <input type="email" class="form-control" placeholder="name@example.com"/>
                    </div><br/>

                        <div class="input-group mb-2">
                            <div class="input-group-prepend">
                                <div class="input-group-text">Your Message</div>
                            </div>
                        <textarea type="email" rows="5" class="form-control" placeholder="name@example.com"></textarea>
                    </div>
                </form>
            </p>
            
            <br/>Developed on demand of completing Chandigarh's University Summer Training Mini Project for July 2022 by <br/><b><i>Devesh Kumar Singh [UID : 21BCS8725]</i></b></p> </center>
        </div>
        </div>
    )
}
